/* 모바일 가로, 테블릿 세로 (해상도 ~ 479px)*/
@media all and (max-width: 767px) {
  .media-wrapper {
    display: grid;
    gap: 0px 0px;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
  }
  .media-grid--col2__11 {
    display: grid;
    gap: 10px 10px;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    /* background-color: rgb(107, 149, 197) ; */
  }
  .media-grid--col3__111 {
    display: grid;
    gap: 20px 20px;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    /* background-color: rgb(107, 149, 197) ; */
  }
  .media-grid--col4__1111 {
    display: grid;
    gap: 10px 10px;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    /* background-color: rgb(107, 149, 197) ; */
  }
  .media-grid--col7__1111 {
    display: grid;
    gap: 10px 10px;
    grid-template-columns: 1fr 0fr 1fr 0fr;
    grid-template-rows: auto;
    /* background-color: rgb(107, 149, 197) ; */
  }
  .media-grid--col3__21 {
    display: grid;
    gap: 0px 0px;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    /* background-color: rgb(107, 149, 197) ; */
  }
  .media-grid--col3__12 {
    display: grid;
    gap: 10px 10px;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    /* background-color: rgb(107, 149, 197) ; */
  }
  .media-span-good-load {
    font-family: Fredoka One;
    font-style: normal;
    font-weight: normal;
    font-size: 40px;
    line-height: 60px;
    letter-spacing: 0.01em;
    color: #ffb300;
  }
  .media-span-group-search {
    font-family: "Sandoll Samliphopangche";
    font-style: normal;
    font-weight: 550;
    font-size: 26px;
    line-height: 22px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #573300;
    padding-top: 10px;
  }
  .media-icon-quote {
    width: 16px;
    height: 12px;
  }
  .media-wrapper-footer {
    padding-left: 10px;
    padding-right: 10px;
    background-color: #f8f8f8;
  }
  /* 공백 */
  .media-blank-bottom {
    height: 40px;
  }
  /* 공백 */
  .media-blank-60 {
    height: 10px;
  }
  /* 공백 */
  .media-blank-50 {
    height: 10px;
  }

  /* 로그인 */
  .media-sign-container {
    width: 100%;
    /* min-height: 500px; */
    background: #ffffff;
    border: 2px solid #ffbf26;
    box-sizing: border-box;
  }
  .media-sign-bottom-box {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    width: 100%;
    height: 137px;
    background: #ffbf26;
    box-sizing: border-box;
  }

  /* 흰색 라운드 박스 */
  .media-container-round-white-padding40 {
    background: #ffffff;
    border-radius: 21px;
    padding: 20px;
  }
  .media-container-round-white-shadow-padding40 {
    flex-grow: 1;
    background: #ffffff;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 28px;
    padding: 20px;
  }
  .media-container-round-F9F9F9-shadow-padding40 {
    padding: 20px;
    background: #f9f9f9;
    border-radius: 20px;
  }
  .media-container-round-white-shadow-padding20 {
    flex-grow: 1;
    background: #ffffff;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 28px;
    padding: 10px;
  }
  .media-container-round-white-shadow-padding10 {
    flex-grow: 1;
    background: #ffffff;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 28px;
    padding: 10px;
  }
  .media-container-round-grey-shadow-padding40 {
    flex-grow: 1;
    background: #f6f4f2;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 28px;
    padding: 10px;
  }
  .media-default-img-size {
    width: 350px;
  }
  .media-column-line {
    border: 1px solid rgba(87, 51, 0, 0.15);
    transform: rotate(90deg);
    width: 80px;
  }
  .media-column-line-blank {
    border: 0px solid rgba(87, 51, 0, 0.15);
    transform: rotate(90deg);
    width: 0px;
  }
  .media-group-add-image-none {
    display: none;
  }
  .popup-style {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    border-radius: 1px;
    background: #ffffff;
    box-shadow: 0px -10px 20px rgba(0, 0, 0, 0.04);
    padding: 20px;
  }
  .media-group-grey-container {
    background: #ececec;
    border-radius: 14px;
    padding: 20px;
  }
  .media-common-row-wrap-hcenter-vstretch {
    display: none;
  }
  .media-common-flex-row-wrap-space-between-vcenter {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-left: 50px;
    margin-right: 50px;
  }
  .media-footer-none {
    display: none !important;
  }
  .media-footer-block {
    display: block !important;
  }
}
